  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.app_chart{
  width: 550px;
  text-align: center;
  margin-bottom: 3%
}

.app_pie{
  height: 250px;
  text-align: center;
}
